import multiCascader from "./mulCheckCascader.vue";
import { GetDeviceTags } from '@/api/label';
export default {
  name: 'Demo',
  components: {
    multiCascader: multiCascader
  },
  data: function data() {
    return {
      configTips: this.$t('list.details.label.labelTip'),
      // 
      configOptions: [{
        value: '1',
        label: '',
        checked: false,
        // 
        multiple: false //    falsetrue

      }],
      commonLength: ''
    };
  },
  mounted: function mounted() {
    this.MulitGetlistBenchmark(); // 
  },
  methods: {
    // item   
    getSelected: function getSelected(val) {
      var strnum = val.length;

      if (strnum !== this.commonLength) {
        // 
        this.$emit('CheckedsIndexCodes', val);
      }

      this.commonLength = val.length;
    },
    MulitGetlistBenchmark: function MulitGetlistBenchmark() {
      var _this = this;

      // 
      GetDeviceTags().then(function (response) {
        _this.configOptions = response.data;

        _this.configOptions.map(function (item) {
          item.label = item.typeName;
          item.value = item.typeName;
          item.children = item.options;
          item.children.map(function (i) {
            i.label = i.name;
            i.value = i.name;
          });
        });
      });
    }
  }
};